	import {  mapMutations  } from 'vuex';
	export default{
		data(){
			return {
				mobile: '',
				password: '',
				logining: false
			}
		},
		onLoad(){
			
		},
		methods: {
			...mapMutations(['login']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack();
			},
			toRegist(){
				this.$api.msg('去注册');
			},
			async toLogin(){
				// this.logining = true;
				const {mobile, password} = this;
				var pattPhone = /^1(3|4|5|6|7|8|9)\d{9}$/;
				if(!pattPhone.test(mobile)){
					uni.showToast({title:"请正确输入手机号码",icon:"none",mask:true})
					return;
				}else if(password == ""){
					uni.showToast({title:"请正确输入密码",icon:"none",mask:true})
					return;
				}
				const sendData = {mobile,password};
				const result = await this.$api.json('userInfo');
				if(result.status === 1){
					this.login(result.data);
                    uni.navigateBack();  
				}else{
					this.$api.msg(result.msg);
					this.logining = false;
				}
			}
		},

	}
